import './OutputPanel.css';
import React from "react";
import GraphemeSplitter from "grapheme-splitter";
import { StyleSingleColor, StyleColoredVowels, StyleSyllabes } from "./SettingsPanel";

export default function OutputPanel(props) {
    if (!props.settings || !props.settings.words) {
        return <></>
    }

    const settings = props.settings;    
    const words = settings.words;

    let wordList = words.map((word, idx) => {
        if (!word || word.trim() === "") {
            return "";
        } else {
            return <WordComponent style={settings.style} fontSize={parseInt(settings.fontSize)} labelWidth={settings.labelWidth} word={word} key={word + "-" + idx} />
        }
    })

    return <div className="bd-app d-flex bg-body">
        <div className="row">
            <div className="col">
                {wordList}
            </div>
        </div>
    </div>
}

function WordComponent(props) {
    const wordRenders = {
        [StyleSingleColor]: (word) => {
            return <>{word}</>
        },
        [StyleColoredVowels]: (word) => {
            const vowels = "aeiou";

            let splitter = new GraphemeSplitter();
            let graphemes = splitter.splitGraphemes(word);

            let letters = graphemes.map((letter, idx) => {
                let normalizedLetter = letter.normalize('NFD')[0];
                let spanClass;
                if (vowels.includes(normalizedLetter)) {
                    spanClass = "vowel"
                }
                else {
                    spanClass = "consonant";
                }
                return <span key={idx} className={spanClass}>{letter}</span>
            })
            return <>{letters}</>
        },
        [StyleSyllabes]: (word) => {
            let syllabes = word.split("-");

            let renderedSyllabes = syllabes.map((syllabe, idx) => {
                let spanClass = "color-1";
                if (idx % 2 === 1) {
                    spanClass = "color-2";
                }

                return <span key={idx} className={spanClass}>{syllabe}</span>
            });

            return <>{renderedSyllabes}</>
        }
    }

    return <div className="word" key={props.word} style={{ fontSize: props.fontSize + "pt", width: props.labelWidth + "em" }}>{wordRenders[props.style](props.word)}</div>

}