import './SettingsPanel.css';
import React, { useState } from "react";

const StyleColoredVowels = "colored-vowels";
const StyleSingleColor = "single-color";
const StyleSyllabes = "syllabes";

const WordStyles = {
    [StyleSingleColor]: "Single Color",
    [StyleColoredVowels]: "Colored Vowels",
    [StyleSyllabes]: "Syllabes"
}

export {
    StyleColoredVowels,
    StyleSingleColor,
    StyleSyllabes
}

export default function SettingsPanel(props) {
    const defaultStyle = StyleColoredVowels;
    const [values, setValues] = useState({
        fontSize: 24,
        labelWidth: 7,
        style: defaultStyle,
        words:""
    });

    function handleFormChange(e) {
        const name = e.target.name;
        const newValues = {
            ...values,
            [name]: e.target.value
        }
        setValues(newValues);
        
        let words = newValues.words.split(/\r?\n/);
        props.onChange({
            words: words,
            fontSize: newValues.fontSize,
            labelWidth: newValues.labelWidth,
            style: newValues.style
        });
    }

    const wordStyleOptionsRendered = Object.entries(WordStyles).map(([k,v]) => {
        return <option value={k} key={k}>{v}</option>
    });

    return <aside className='settings-panel bg-light sticky-xl-top text-muted align-self-start no-print'>
        <h2 className="h2 pt-4 pb-3 mb-4 border-bottom">Settings</h2>

        <form onChange={handleFormChange}>
            <div class="row">
                <div className="col">
                    <label htmlFor="word-style-input">Style</label>
                    <select className="form-control" id="word-style-input" name="style" defaultValue={defaultStyle}>
                        {wordStyleOptionsRendered}
                    </select>
                </div>
            </div>

            <div class="row">
                <div className="col">
                    <label htmlFor="word-font-size">Font Size</label>
                    <input className="form-control" type="number" id="word-font-size" name="fontSize" defaultValue={values.fontSize}/>
                </div>

                <div className="col">
                    <label htmlFor="word-label-width">Label Width</label>
                    <input className="form-control" type="number" id="word-label-width" name="labelWidth" defaultValue={values.labelWidth}/>
                </div>
            </div>

            <div class="form-group">
                <label htmlFor="words-textarea">Words</label>
                <textarea name="words" className="form-control" id="words-textarea" rows="20" defaultValue={values.words} />
            </div>
        </form>

        <button type="button" className="mt-3 btn btn-primary" onClick={() => window.print() }>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-printer" viewBox="0 0 16 16">
                <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"></path>
                <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"></path>
            </svg>
            <span class="px-2">Print</span>
        </button>
    </aside>
}
